	export default {
		data() {
		return {
			authKey:'',
			userInfo:'',
			part:'',
			rules:'',
			popo:false
		};
	},
	onLoad() {
		this.authKey = uni.getStorageSync('authKey');
		this.userInfo = uni.getStorageSync('userInfo');
		this.join();
	},
	methods:{
		// 获取余额及规则
		join(){
			uni.request({
				url: this.http+'copemate/homepage',
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {},
				success: res => {
					console.log(res.data)
					if (res.data.code == 200) {
                         this.part = res.data.data.income
						 this.rules= res.data.data.rule
					}
				},
			});
		},
		withdraw(){
			if(this.part > this.rules.commission_withdraw_min){
				uni.navigateTo({
					url: '/pages/mine/partnership/withdraw'
				});
			}else{
				this.popo = true
			}
		},
		close(){
			this.popo = false
		},
		godetail(){
			this.popo = false
		},
		returns(){
			uni.navigateTo({
				url: '/pages/mine/partnership/returnsDetailed'
			});
		}
	}
}
